.error-message-container {
    position: fixed;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: red;
    padding: 10px;
    color: #fff;
    z-index: 999;
    width: 100%;
    height: 65px;
    transition: top var(--speed) ease;
}
