.popular {
    display: flex;
    flex: 4;
    flex-direction: column;
    position: relative;
}

.popular-slide-item {
    width: 100%;
    height: 100%;
    position: absolute;
}

.popular-image {
    width: 100%;
    height: 100%;
    transition: all 1s ease;
}

.popular-slide-details {
    left: 10%;
    width: 80%;
    position: absolute;
    bottom: 10%;
    background-color: rgba(0, 0, 0, 0.2);
    padding: 6px;
}

.popular-slide-title {
    color: #fff;
    text-align: center;
    user-select: none;
}

.popular-slide-author,
.popular-slide-date {
    color: #fff;
    padding: 6px;
    font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
    user-select: none;
}

.popular-slide-right-icon,
.popular-slide-left-icon {
    position: absolute;
    top: 45%;
    background-color: rgba(0, 0, 0, 0.25);
    display: flex;
    transition: all var(--speed) ease;
    cursor: pointer;
}

.popular-slide-right-icon {
    right: 0%;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
}

.popular-slide-left-icon {
    left: 0%;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
}

.popular-slide-left-icon:hover {
    background-color: rgba(0, 0, 0, 0.75);
}

.popular-slide-right-icon:hover {
    background-color: rgba(0, 0, 0, 0.75);
}

.popular-slide-icon {
    color: #fff;
    font-size: 40px;
}

.fade {
    -webkit-animation-duration: 1.5s;
    -webkit-animation-name: fade;
    animation-name: fade;
    animation-duration: 1.5s;
}

@keyframes fade {
    from {
        opacity: 0.5;
    }
    to {
        opacity: 1;
    }
}
